import React, { useEffect, useState } from 'react'
import { CCard, CCardBody, CCardHeader, CCol, CRow } from '@coreui/react'
import CIcon from '@coreui/icons-react'
import api from '../../api/default';

import usersData from './UsersData'

const User = ({match}) => {

  const [user, setUser] = useState({});
  const [userDetails, setUserDetails] = useState([]);

  useEffect(() => {
    api.get(`/user/${match.params.id}`)
    .then( ({data}) => setUser(data))
  }, [])

  useEffect(() => {
    setUserDetails(user ? Object.entries(user) : 
    [['id', (<span><CIcon className="text-muted" name="cui-icon-ban" /> Not found</span>)]])
  }, [user])
  
  return (
    <CRow>
      <CCol lg={6}>
        <CCard>
          <CCardHeader>
            User id: {match.params.id}
          </CCardHeader>
          <CCardBody>
              <table className="table table-striped table-hover">
                <tbody>
                  {/* {
                    userDetails.map(([key, value], index) => {
                      if (typeof value !== 'object') return (
                        <tr key={index.toString()}>
                          <td>{`${key}:`}</td>
                          <td><strong>{value.toString()}</strong></td>
                        </tr>
                      )
                    })
                  } */}
                  <tr>
                    <td>Nome:</td>
                    <td><strong>{user.name}</strong></td>
                  </tr>
                  <tr>
                    <td>CPF:</td>
                    <td><strong>{user.cpf}</strong></td>
                  </tr>
                  <tr>
                    <td>E-mail:</td>
                    <td><strong>{user.email}</strong></td>
                  </tr>
                  <tr>
                    <td>Participante confirmado?</td>
                    <td><strong>{user.isConfirmado ? 'Sim' : 'NÃ£o'}</strong></td>
                  </tr>
                </tbody>
              </table>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  )
}

export default User
